.footer{
    font-family: 'Raleway', sans-serif;
    display: grid;
    place-items: center;
    background-color: rgb(45, 51, 59);
    padding: 20px 0;
}

.info-footer{
    font-size: 17px;
    color: rgb(255,255,255);
}

.info-footer a{
    text-decoration: none;
    color: rgb(255,255,255);
}

.info-footer a:hover{
    text-decoration: underline;
}